import type { Metadata } from "next";
import { Geist, Geist_Mono } from "next/font/google";
import ".=@/globals.css";

// Modern font optimization (Standard in 2025/2026)
const geistSans = Geist({
  variable: "--font-geist-sans",
  subsets: ["latin"],
});

const geistMono = Geist_Mono({
  variable: "--font-geist-mono",
  subsets: ["latin"],
});

// Centralized SEO management
export const metadata: Metadata = {
  title: {
    template: "%s | My Next.js Store",
    default: "My Next.js Store",
  },
  description: "Modern E-commerce built with Next.js 15",
};

export default function RootLayout({
  children,
}: Readonly<{
  children: React.ReactNode;
}>) {
  return (
    <html lang="en">
      <body className={`${geistSans.variable} ${geistMono.variable} antialiased`}>
        {/* Persistent Layout Elements (e.g., Navbar) */}
        <header className="p-4 bg-white border-b">
          <nav className="max-w-7xl mx-auto flex justify-between">
            <h1 className="font-bold">BrandLogo</h1>
            <div className="flex gap-4">
              <a href="/products">Products</a>
              <a href="/cart">Cart</a>
            </div>
          </nav>
        </header>

        <main className="min-h-screen">{children}</main>

        <footer className="p-4 text-center border-t">
          Â© 2026 Ecommerce Inc.
        </footer>
      </body>
    </html>
  );
}
